import { Cheval } from "./cheval";

export class notification {
    constructor(message: string, titre: string, createdOn: Date, type: string, groupeUtilisateur: string, cheval: Cheval) {
        this.message = message;
        this.titre = titre;
        this.createdOn = createdOn;
        this.type = type;
        this.groupeUtilisateur = groupeUtilisateur;
        this.cheval = cheval;
    }
    message: string;
    titre: string;
    createdOn: Date;
    createdBy: number;
    type: string;
    groupeUtilisateur: string;
    cheval: Cheval;
}